.social {
    @include ul();
    padding: 6px 0 0;
}

.social__link {
    @include a();
    text-align: left;
    display: flex;

    & svg {
        margin-right: 15px;
    }
}

@media (max-width: $desktop) {
    .social {
        padding-top: 0;
    }

    .social__link svg {
        margin-right: 12px;
        width: 20px;
        height: 21px;
    }
}

@media (max-width: $tablet) {
    .social {
        margin-left: -2px;
        gap: 27px;
    }

    .social__link svg {
        margin-right: 0;
        width: 20px;
        height: 21px;
    }
}
